html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

html, body {
  margin: 0;
  height: 100%;
}

body {
  background-color: $body-bg;
  font-family: "ProximaNovaRegular", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.wrapper {
  height: 100%;
  min-width: 320px;
}

.l-content {
  box-sizing: border-box;
  min-height: 100%;
  padding-bottom: 40px;
  overflow: hidden;
}

.footer {
  height: 40px;
  margin-top: -40px;
  background-color: #fff;
  text-align: center;
  line-height: 40px;
}

.visually-hidden {
  display: none !important;
}

.content {
  width: 100%;
}

.c-container {
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.flex-container {
  display: flex;
  justify-content: space-between;
  align-content: stretch;
  flex-wrap: wrap;
  width: 100%;
  margin: 0 auto;
  padding: 40px 15px;
}

.main-header {
  background-color: $main-accent;
  color: #FFFFFF;
  .logo {
    display: none;
  }
  .user-info {
    position: absolute;
    right: 25px;
    top: 21px;
  }
}
.header-inner {
  display: flex;
  justify-content: space-between;
  height: 72px;
  padding: 0 25px;
  position: relative;
  align-items: center;
}
.h-gamburger {
  width: 35px;
  height: 25px;
  position: relative;
  cursor: pointer;
  transition: all .2s ease;
  &:before, &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 5px;
    background-color: #fff;
    border-radius: 3px;
  }
  &:before {
    top: 0;
  }
  &:after {
    bottom: 0;
  }
  .center-line {
    position: absolute;
    top: 10px;
    display: block;
    width: 75%;
    height: 5px;
    background-color: #fff;
    border-radius: 3px;
    transition: all .2s ease;
    right: 25%;
  }
  &:hover {
    .center-line {
      right: 0;
    }
  }
}

.l-content.vis-mobile{
  .h-gamburger {
    transform: rotateZ(180deg);
  }
}
.nav {
  width: 100%;
  display: none;
  border-top: 1px solid $mask-color;
  padding-top: 15px;
  &_item {
    transition: background-color .2s ease;
    &__link {
      display: inline-block;
      width: 100%;
      padding: 10px 25px;
      font-family: "ProximaNovaSemibold";
      font-size: 16px;
      letter-spacing: 2px;
      line-height: 34px;
      color: #B0EAD2;
      text-decoration: none;
      text-transform: uppercase;
      &:hover {
        text-decoration: none;
        color: #fff;
      }
    }
    &:hover {
      background-color: rgba(235, 235, 235, 0.22);
    }
    &.active {
      .nav_item__link {
        color: #fff;
      }
    }
  }
}

.user {
  &-nav-btn {
    position: relative;
    height: 32px;
    width: 32px;
    border: 2px solid;
    border-radius: 50%;
    text-align: center;
    cursor: pointer;
    z-index: 10;
    transition: border-color .2s ease;
    .registered-user {
      display: block;
      height: 100%;
      border-radius: 50%;
      line-height: 30px;
      font-size: 20px;
      background-size: cover;
    }
    .unregistered-user {
      display: block;
      height: 100%;
      border-radius: 50%;
      background-size: cover;
    }
    &:hover {
      border: 2px solid rgba(255, 255, 255, 0.7);
    }
  }
  &-nav {
    display: none;
    position: absolute;
    right: 0;
    top: 44px;
    width: 200px;
    padding: 15px 0;
    background: #fff;
    box-shadow: 0 2px 10px rgba(0, 0, 0, .2);
    z-index: 50;
    &_list {
      list-style-type: none;
      padding: 0;
    }
    &:before {
      content: '1';
      position: absolute;
      right: 10px;
      top: -4px;
      width: 10px;
      height: 10px;
      background-color: #fff;
      transform: rotateZ(45deg);
    }
    &_item {
      text-align: center;
      transition: all .4s ease;
      &:hover {
        background-color: $main-accent;
      }
      &__link {
        display: inline-block;
        width: 100%;
        padding: 7px 15px;
        color: $text-medium;
        font-size: 12px;
        text-transform: uppercase;
        transition: all .2s ease;
        &:hover {
          text-decoration: none;
          color: #fff;
        }
      }
    }
    &_buttons {
      border-top: 1px solid #e7e7e7;
      padding-top: 15px;
      display: flex;
      justify-content: space-around;
      .btn {
        background-color: $main-accent;
        color: #fff;
        font-size: 12px;
        &:hover {
          background-color: lighten($main-accent, 10%);
        }
      }
    }
  }
}


.mobile-toggler {
  display: none;
  text-transform: uppercase;
  padding: 0 25px;
  height: 60px;
  line-height: 60px;
  color: #fff;
  background-color: $secondary-accent;
  border-bottom: 1px solid $mask-color;
  cursor: pointer;
  &_icon {
    font-size: 24px;
    line-height: 60px;
  }
  &_title {
    margin-left: 10px;
    font-size: 16px;
    letter-spacing: 1px;
  }
  &:hover {
    background-color: $main-accent;
  }
}

.custom-input {
  border: none;
  border-radius: 8px;
  position: relative;
  height: 36px;
  line-height: 36px;
  padding: 0 10px;
  transition: all .3s ease;
  &:focus {
    outline: none;
  }
}

.custom-input-wrap {
  display: inline-block;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 2px;
    background-color: $main-accent;
    transition: width .4s ease;
  }
  &.focus {
    &:after {
      width: 100%;
    }
  }
  &.filled {
    &:after {
      width: 100%;
    }
  }
}

.content-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: stretch;
  align-items:stretch;
  padding: 15px 5px;
}

.btn-primary {
  color: #fff;
  background-color: $main-accent;
  border-color: $main-accent;
  &:hover {
    background-color: darken($main-accent, 20%);
    border-color: darken($main-accent, 20%);
  }
  &:disabled {
    background-color: lighten($main-accent, 20%);
    border-color: lighten($main-accent, 20%);
  }
}

.chart-nav {
  max-width: 480px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  list-style-type: none;
  padding: 5px 10px;
  margin-bottom: 0;
}


.c-btn-link {
  position: relative;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: $main-accent;
  font-size: 16px;
  &:before {
    content: '';
    position: absolute;
    bottom: -1px;
    left: 0;
    width: 0;
    height: 2px;
    opacity: .6;
    background-color: $main-accent;
    transition: width .2s ease;
  }
  &:focus, &:active {
    outline: none;
    &:before {
      opacity: 1;
  }
  }
  &:hover {
    &:before {
      width: 100%;
    }
  }
}
.c-btn {
  display: block;
  position: relative;
  line-height: 55px;
  height: 55px;
  padding: 0 25px;
  font-family: "ProximaNovaSemibold";
  font-size: 15px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  border: none;
  border-radius: 28px;
  background-color: $main-accent;
  cursor: pointer;
  transition: all .2s ease;
  overflow: hidden;
  &:focus {
    outline: none;
  }
  &:hover {
    color: #fff;
    text-decoration: none;
    background-color: lighten($main-accent, 10%);
  }
  &:after {
    content: "";
    background: rgba(0, 0, 0, 0.35);
    display: block;
    position: absolute;
    padding-top: 150%;
    padding-left: 300%;
    margin-left: -40px !important;
    margin-top: -60%;
    opacity: 0;
    transition: all 0.8s;
    border-radius: 24px;
  }
  &:active {
    &:after {
      padding: 0;
      margin: 0;
      opacity: 1;
      transition: 0s;
    }
  }
  &.contrast {
    background-color: $secondary-accent_contrast;
    &:hover {
      background-color: lighten($secondary-accent_contrast, 10%);
    }
  }
}

.modal-p-description {
  padding-right: 0 !important;
  .modal-header {
    border-bottom: none;
  }
  .modal-title {
    width: 100%;
    font-size: 14px;
    padding-top: 15px;
    text-transform: uppercase;
    color: #3D4D53;
    text-align: center;
  }
  .close {
    flex-shrink: 0;
    padding: 0;
    margin: 0;
    text-align: center;
    line-height: 22px;
    font-size: 28px;
    color: #CACCD5;
    outline: none;
    transition: .4s ease;
    &:hover {
      transform: rotateZ(180deg);
    }
  }
  .modal-body {
    padding: 0 30px;
    color: #344750;
    .title {
      font-size: 18px;
      color: #2A3245;
      text-align: center;
    }
    p {
      text-align: justify;
    }
  }
  .modal-dialog {
    margin: 60px 30px 40px;
  }
}

@media (min-width: 576px) {
  .c-container, .flex-container {
    max-width: 540px;
  }
  .modal-p-description {
    .modal-dialog {
      max-width: 500px;
      margin: 100px auto;
    }
  }
}

@media (min-width: 768px) {
  .c-container, .flex-container {
    max-width: 720px;
  }

}

@media (min-width: 992px) {
  .c-container, .flex-container {
    max-width: 910px;
  }
  .main-header {
    position: relative;
    .logo {
      display: block;
      width: $sidebar-width;
      height: 72px;
      a {
        display: inline-block;
        margin-top: 14px;
      }
      img {
        width: 164px;
      }
    }
    .user-info {
      right: 20px;
      top: 20px;
    }
  }
  .header-inner {
    width: 100%;
  }
  .nav {
    display: flex!important;
    position: absolute;
    top: 0;
    padding: 0;
    left: $sidebar-width;
    border-top: none;
    &_item {
      &__link {
        width: auto;
        display: inline-block;
        padding: 0 18px;
        font-size: 13px;
        line-height:72px;
      }
      &.active {
        background-color: $mask-color;
      }
    }
  }
  .user-nav {
    right: 1px;
  }

  .mobile-toggler, .h-gamburger {
    display: none!important;
  }
  .has-sidebar .content {
    float: right;
    width: calc(100% - 264px);
  }
  .modal-p-description {
    .modal-header {
      padding: 42px;
    }
    .modal-title {
      font-size: 18px;
    }
    .modal-dialog {
      max-width: 920px;
      margin: 100px auto;
    }
    .modal-body {
      padding: 0 130px 70px;
      .title {
        font-size: 24px;
      }
      p {
        padding-top: 20px;
        font-size: 16px;
        line-height: 28px;
      }
    }
    .close {
      width: 32px;
      height: 32px;
      line-height: 29px;
      font-size: 36px;
      border: 3px solid;
      border-radius: 50%;
    }
  }
}

@media (min-width: 1060px) {
  .c-container, .flex-container, .container {
    max-width: 1030px;
  }
}

@media (min-width: 1200px) {
  .c-container, .flex-container, .container {
    max-width: 1140px;
  }
  .product-page {
    justify-content: center;
  }
}
@media (min-width: 1320px) {
  .c-container, .flex-container, .container {
    max-width: 1310px;
  }
}
@media (min-width: 1500px) {
  .c-container, .flex-container, .container {
    max-width: 1440px;
  }
  .product-page {
    justify-content: space-between;
  }
}

@media (min-width: 1860px) {
  .c-container, .flex-container, .container {
    max-width: 1830px;
  }
}