
/*--- sliders ---*/

.sledrers-title {
  color: $text-medium;
  font-family: "ProximaNovaSemibold";
  text-transform: uppercase;
  text-align: center;
  font-size: 18px;
  letter-spacing: 2px;
  line-height: 20px;
  margin: 40px 0 30px;
}



.owl-carousel {
  position: relative;
  width: 100%;
  margin: 0 auto;
  .owl-nav  {
    position: absolute;
    top: 50%;
    left: -45px;
    margin-top: -40px;
    width: calc(100% + 90px);
    button.owl-prev, button.owl-next {
      width: 40px;
      height: 40px;
      color: $main-accent;
      border: 2px solid;
      border-radius: 50%;
      font-size: 20px;
      line-height: 38px;
      &.disabled{
        color: $disabled;
        cursor: default;
      }
      &:focus {
        outline: none;
      }
    }
    button.owl-next {
      float: right;
    }
  }
  .owl-stage {
    display: flex;
    .product-card {
      height: 100%;
    }
  }
}


@media (min-width: 992px)  {
  .owl-carousel {
    width: calc(100% - 160px);
    .owl-nav  {
      left: -60px;
      width:  calc(100% + 120px);
    }
  }
}