

.product-card {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 216px;
  margin: 0 8px 16px;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0 5px 8px 1px rgba(237,239,241,0.5);
  &_preview {
    display: inline-block;
    max-width: 100%;
    border-radius: 4px 4px 0 0;
  }
  .text-wrap {
    padding: 20px 22px;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    justify-content: space-between;
  }
  &_title {
    margin-bottom: 4px;
    font-size: 16px;
    line-height: 19px;
    color: $text-medium;
    font-family: "ProximaNovaSemibold";
  }
  &_category {
    font-size: 14px;
    line-height: 17px;
    color: $text-light;
  }
  &_price {
    font-size: 15px;
    line-height: 18px;
    color: $text-medium;
    font-family: "ProximaNovaSemibold";
  }
  &_discount-price {
    font-size: 15px;
    line-height: 18px;
    color: $text-light;
    font-family: "ProximaNovaSemibold";
  }
  &_created {
    float: left;
    margin-bottom: 10px;
    font-size: 12px;
    line-height: 14px;
    color: $text-light;
  }
  &_strength {
    position: relative;
    float: right;
    &:hover .product-card_index {
      background-color: $main-accent;
      color: #fff;
      border: 2px solid $main-accent;
    }
  }
  &_index {
    display: block;
    min-width: 24px;
    height: 24px;
    padding: 0 5px;
    border-radius: 12px;
    border: 2px solid #EBECF0;
    color: $main-accent;
    background-color: #fff;
    font-size: 13px;
    line-height: 20px;
    font-family: "ProximaNovaSemibold";
    text-align: center;
    transition: all .2s ease;
    cursor: pointer;
  }
}

.c-tooltip {
  position: absolute;
  min-height: 42px;
  max-width: 210px;
  min-width: 180px;
  //white-space: nowrap;
  margin: 0 0 5px 0;
  padding: 11px 8px 11px 15px;
  border-radius: 4px;
  font-size: 14px;
  line-height: 20px;
  text-transform: none;
  text-align: center;
  font-family: "ProximaNovaRegular";
  color: #fff;
  display: block;
  z-index: 20;
  transition: all .3s cubic-bezier(.3, 0, 0, 1);
  transform: rotateX(40deg) scale(.8);
  -webkit-backface-visibility: hidden;
  background-color: $main-accent;
  box-shadow: 0 5px 8px 1px rgba(237,239,241,0.5);
  visibility: hidden;
  opacity: 0;
  &_text {
    padding: 0;
    margin: 0;
    font-size: 12px;
    line-height: 16px;
    text-align: justify;
  }
  .to-detail-info {
    display: inline-block;
    height: 20px;
    width: 20px;
    margin-left: 4px;
    border-radius: 50%;
    border: 2px solid #26A572;
    color: #fff;
    font-size: 12px;
    line-height: 18px;
    text-align: center;
    &:hover {
      background-color: #26A572;
      text-decoration: none;
    }
  }
  &:after {
    content: "";
    position: absolute;
    right: 36px;
    width: 0;
    border: 5px solid transparent;
  }
  &:before {
    content: "";
    position: absolute;
    left: -5%;
    right: -5%;
    border-radius: 15px;
    z-index: -10;
  }
}
.c-tooltip_large {
  position: absolute;
  width: 280px;
  padding: 28px 38px;
  border-radius: 8px;
  text-transform: none;
  font-family: "ProximaNovaRegular";
  color: $text-medium;
  z-index: 20;
  transition: all .3s cubic-bezier(.3, 0, 0, 1);
  transform: rotateX(40deg) scale(.8);
  -webkit-backface-visibility: hidden;
  background-color: #fff;
  box-shadow: 0 5px 8px 1px rgba(237,239,241,0.5);
  visibility: hidden;
  opacity: 0;
  &__text {
    padding: 0;
    margin: 0;
    font-size: 15px;
    line-height: 25px;
    text-align: justify;
  }
  &:before {
    content: "";
    position: absolute;
    left: -5%;
    right: -5%;
    top: -5%;
    bottom: -5%;
    border-radius: 15px;
    background-color: transparent;
  }
}
.c-tooltip.to-top {
  bottom: 28px;
  right: -28px;
  transform-origin: 90% 120%;
  &:after {
    top: 100%;
    border-top-color: $main-accent;
  }
  &:before {
    top: -5%;
    bottom: -30%;
  }
}
.c-tooltip.to-bottom {
  top: 28px;
  right: -30px;
  transform-origin: 90% 0%;
  &:after {
    top: -9px;
    border-bottom-color: $main-accent;
  }
  &:before {
    top: -30%;
    bottom: -5%;
  }
}

.has-c-tooltip {
  cursor: pointer;
  position: relative;
}
.has-c-tooltip:focus .c-tooltip,
.has-c-tooltip:hover .c-tooltip,
.has-c-tooltip:focus .c-tooltip_large,
.has-c-tooltip:hover .c-tooltip_large{
  transform: rotateX(0) scale(1);
  pointer-events: auto;
  visibility: visible;
  opacity: 1;
}

@media (min-width: 460px) {
  .c-tooltip_large {
    width: 390px;
  }
}