$link-bg: #fff;

.store-card {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 10px;
  margin: 0 5px 16px;
  border-radius: 4px;
  background-color: #FFFFFF;
  box-shadow: 0 5px 8px 1px rgba(237, 239, 241, 0.5);
  &_preview--wrap {
    flex-basis: 104px;
    flex-shrink: 0;
    margin-right: auto;
    margin-left: auto;
    width: 200px;
    height: 140px;
    border-radius: 4px;
    overflow: hidden;
  }
  &_preview {
    width: 100%;
    display: inline-block;
  }
  &_title {
    margin-bottom: 0;
    color: $text-medium-2;
    font-size: 18px;
    font-family: ProximaNovaSemibold;
    overflow: hidden;
  }
  &_link {
    display: inline-block;
    position: relative;
    width: 218px;
    color: $link-default;
    background-color: $link-bg;
    text-decoration: none;
    white-space: nowrap;
    font-size: 14px;
    overflow: hidden;
    &:after {
      content: '';
      position: absolute;
      right: 0;
      top: 0;
      width: 40px;
      height: 100%;
      background: linear-gradient(to right, rgba(255, 255, 255, 0.001), $link-bg);
    }
  }
  &_description {
    flex-grow: 1;
    padding-top: 18px;
    color: $text-medium-3;
    font-size: 14px;
    line-height: 18px;
    margin-bottom: 14px;
  }
  &_data {
    width: 100%;

    tr {
      margin-bottom: 20px;
    }
    tr:not(:last-child) {
      td {
        padding-bottom: 8px;
      }
    }
    td:not(:last-child) {
      width: 30%;
    }
    .key {
      display: block;
      font-size: 12px;
      color: $text-light;
      text-transform: uppercase;
      font-family: ProximaNovaSemibold;
    }
    .value {
      font-size: 16px;
      color: $text-medium;
      font-family: ProximaNovaSemibold;
    }
  }
}

@media (min-width: 425px) {
  .store-card {
    width: 394px;
    padding: 24px;
    &_heading {
      display: flex;
      flex-direction: row-reverse;
    }
    &_preview--wrap {
      margin-left: inherit;
      margin-right: 24px;
      width: 104px;
      height: 72px;
    }
    &_title {
      font-size: 20px;
    }
  }
}