/*  POPULAR APPS */


.popular-apps {
  background-color: #fff;
  margin-top: 55px;
  padding: 60px 0;
  &_title {
    font-size: 18px;
    margin-bottom: 30px;
    text-transform: uppercase;
    font-family: "ProximaNovaSemibold";
    color: #3D4D53;
    text-align: center;
    letter-spacing: 2px;
  }
  &_item {
    margin-bottom: 40px;
  }
  &_icon {
    display: block;
    width: 57px;
    height: 57px;
    margin: 10px auto;
  }
  &_text {
    padding: 0 20px;
  }
  .app-name {
    margin-bottom: 0;
    font-family: "ProximaNovaSemibold";
    color: $text-medium;
    font-size: 15px;
    text-align: center;
  }
  .app-tag {
    margin: 1px 0 0;
    color: $text-light;
    font-size: 11px;
    text-align: center;
    text-transform: uppercase;
  }
  &_description {
    padding: 12px 0 6px;
    margin-bottom: 0;
    color: #737A84;
    font-size: 14px;
    line-height: 18px;
    text-align: justify;
  }
  &_link {
    color: #3581C6;
    font-size: 14px;
    &:hover {
      text-decoration: none;
      opacity: .6;
    }
  }
  &_form {
    max-width: 497px;
    margin: 0 auto;
    .form-group {
      position: relative;
      display: block;
      height: 40px;
      border-radius: 25px;
      background-color: #E7E9F1;
      overflow: hidden;
      -webkit-mask-image: -webkit-radial-gradient(white, black); /////////FIX FOR BORDER-RADIUS !!!
      &:before {
        content: '';
        width: 110%;
        height: 100%;
        position: absolute;
        background-color: $main-accent;
        transform: skew(45deg) translateX(-105%) ;
        transition: all 1s ease;
      }
      &.focus, &.filled {
        &:before {
          bottom: 0;
          transform: skew(-25deg) translateX(-5%);
        }
      }
      &.has-errors:before {
        background-color: $attent-accent;
      }
    }
  }
  .search-input {
    position: absolute;
    width: calc(100% - 4px);
    height: 36px;
    margin: 2px 0 0 2px;
    border: none;
    border-radius: 25px;
    line-height: normal;
    padding: 0 50px 0 20px ;
    color: #747C8D;
    font-size: 15px;
    background-color: #fff;
    &:focus {
      outline: none;
    }
  }
  .search-btn {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    width: 50px;
    border: none;
    border-radius: 0 25px 25px 0;
    cursor: pointer;
    background-color: transparent;
    transition: background .2s ease;
    &:hover {
      background-color: rgba(225, 225, 225, 0.3);
    }
    &:focus {
      outline: none;
    }
  }
  .icon-search {
    color: $main-accent;
    font-size: 20px;
  }
}
.popular-apps_nav {
  max-width: 100%;
  margin-top: 25px;
  justify-content: center;
  font-size: 15px;
  .information-card_nav__button {
    padding: 5px 15px;
  }
}




@media (min-width: 450px) {
  .popular-apps {
    &_item {
      display: flex;
    }
    &_icon {
      margin: 0;
    }
    .app-name {
      font-size: 16px;
      text-align: left;
    }
    .app-tag {
      text-align: left;
    }
    &_link {
      font-size: 15px;
    }
    &_form {
      .form-group {
        height: 50px;
      }
    }
    .search-input {
      height: 46px;
      padding: 0 70px 0 30px ;
    }
    .search-btn {
      width: 60px;
    }
    .icon-search {
      font-size: 24px;
    }
  }
  .popular-apps_nav {
    margin-top: 50px;
    margin-bottom: 50px;
  }
}

@media (min-width: 768px) {

}
@media (min-width: 992px) {
  .popular-apps_nav {
    .information-card_nav__button {
      font-size: 15px;
      letter-spacing: 1.15px;
    }
  }
}