    line-height: 16px; }
  .preloader_progress {
    position: relative;
    width: 100%;
    height: 5px;
    border-radius: 3px;
    background-color: #fff; }
    .preloader_progress:before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 5px;
      border-radius: 3px;
      background-color: #27C686;
      animation: preload 2s ease infinite; }

@media (min-width: 460px) {
