    font-size: 13px;
    letter-spacing: 1px; } }

@keyframes preload {
  0% {
    transform: scaleX(0);
    transform-origin: 0 50%; }
  35% {
    transform: scaleX(1);
    transform-origin: 0 50%; }
  36% {
    transform: scaleX(1);
    transform-origin: 100% 50%; }
  71% {
    transform: scaleX(0);
    transform-origin: 100% 50%; }
  100% {
    transform: scaleX(0);
    transform-origin: 100% 50%; } }
