/* LOGIN FORM */

.login-container {
  min-width: 320px;
  width: 100%;
  margin: 30px auto 0;
}
.form-block {
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 4px;
  background-color: #FFFFFF;
  box-shadow: 0 5px 18px 1px rgba(237,239,241,0.5);
  &_title {
    margin-bottom: 20px;
    color: $text-medium-2;
    font-family: "ProximaNovaSemibold";
    font-size: 16px;
    text-align: center;
  }
  .c-btn {
    width: 100%;
    max-width: 300px;
    margin-left: auto;
    margin-right: auto;
  }
  .logo {
    margin-bottom: 55px;
    text-align: center;
    img {
      max-width:240px;
    }
  }
  .form-control {
    height: 48px;
    line-height: 48px;
    font-size: 15px;
    color: $text-medium-2;
    border: 2px solid #E9EBF1;
    border-radius: 8px;
    box-shadow: none;
  }
  .form-group {
    position: relative;
    overflow: hidden;
    &+ .form-group {
      margin-top: 18px;
    }
    &.has-error {
      .form-control {
        color: $error;
        border-color: $error;
      }
    }
  }
  .password-recovery {
    display: inline-block;
    width: 100%;
    text-align: center;
    font-size: 14px;
    color: $text-medium-3;
    transition: color .2s ease;
    &:hover {
      opacity: .8;
      text-decoration: none;
    }
    &:active {
      opacity: .8;
    }
    &.accent {
      color: $main-accent;
    }
  }
  & +.form-block {
    margin-top:15px;
  }
  .error-message {
    display: block;
    margin: 16px 0;
    color: $error;
    height: 0;
    line-height: 30px;
    font-size: 15px;
    text-align: center;
    opacity: 0;
    transition: height .2s .2s ease, opacity .2s ease;
  }
}
.log-in {
  padding-top: 40px;
  padding-bottom: 40px;
  .c-btn {
    margin-bottom: 40px;
  }
  &.has-error .error-message {
    height: 30px;
    opacity: 1;
    transition: height .2s ease, opacity .2s .2s ease;
  }
}
.sign-up {
  padding-top: 30px;
  padding-bottom: 65px;
}

@media (min-width: 450px) {
  .login-container {
    max-width: 448px;
    margin: 80px auto 0;
  }
  .form-block {
    padding-left: 74px;
    padding-right: 74px;
  }
}