/* PRELOADER */

.preloader {
  max-width: 398px;
  padding: 10px 10px 30px;
  margin: 0 auto;
  &_image {
    display: block;
    width: 48px;
    margin: 0 auto 20px;
  }
  &_title {
    margin-bottom: 15px;
    color: $text-light;
    font-family: "ProximaNovaSemibold";
    font-size: 12px;
    text-transform: uppercase;
    text-align: center;
    line-height: 16px;
  }
  &_progress {
    position: relative;
    width: 100%;
    height: 5px;
    border-radius: 3px;
    background-color: #fff;
    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 5px;
      border-radius: 3px;
      background-color: $main-accent;
      animation: preload 2s ease infinite;
    }
  }
}

@media (min-width: 460px) {
  .preloader {
    &_title {
      font-size: 13px;
      letter-spacing: 1px;
    }
  }
}

@keyframes preload {

  0% {
    transform: scaleX(0);
    transform-origin: 0 50%;
  }
  35% {
     transform: scaleX(1);
     transform-origin: 0 50%;
   }
  36% {
    transform: scaleX(1);
    transform-origin: 100% 50%;
  }
  71% {
    transform: scaleX(0);
    transform-origin: 100% 50%;
  }
  100% {
    transform: scaleX(0);
    transform-origin: 100% 50%;
  }
}

