
.product-info {
  width: 100%;
  margin-bottom: 20px;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 5px 8px 1px rgba(237, 239, 241, 0.5);
  overflow: hidden;
  .image-wrap {
    margin-bottom: 20px;
    img {
      display: block;
      max-width: 100%;
    }
  }
  .product-description {
    padding: 15px 32px 30px;
    text-align: left;
    .title {
      text-align: left;
      font-size: 24px;
      color: $text-medium;
      font-family: "ProximaNovaSemibold";
    }
    &_list {
      text-align: left;
      margin: 15px 0 20px;
      padding: 0;
      list-style-type: none;
      font-size: 14px;
      line-height: 30px;
      .key {
        font-family: "ProximaNovaSemibold";
      }
      .key {
        font-size: 15px;
      }
    }
  }
}

.product-statistic {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  flex-wrap: wrap;

}

.information-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 25px 0 35px;
  margin-bottom: 20px;
  border-radius: 4px;
  box-shadow: 0 5px 8px 1px rgba(237, 239, 241, 0.5);
  background-color: #fff;
  &_title {
    margin-top: 18px;
    padding-left: 25px;
    font-size: 18px;
    font-family: "ProximaNovaBold";
    text-transform: uppercase;
    letter-spacing: 2px;
    &:first-line {
      font-family: "ProximaNovaRegular";
    }
  }
  &.chart-container {
    width: 100%;
  }
  &.table-container {
    width: 100%;
  }
  &_nav__button {
    background-color: transparent;
    text-transform: uppercase;
    color: $text-light;
    font-family: ProximaNovaSemibold;
    font-size: 13px;
    line-height: 16px;
    border: none;
    cursor: pointer;
    &.active {
      color: $main-accent;
    }
    &:hover {
      opacity: .8;
    }
    &:focus {
      outline: none;
    }
  }
}


.information-card_table {
  font-size: 15px;
  width: 440px;
  margin-left: auto;
  margin-right:auto;
  text-align: center;
  font-family: ProximaNovaSemibold;
  tr:nth-child(even) {
    background-color: rgba(245, 246, 250, 0.74);
  }
  tr {
    height: 34px;
    &:hover {
      background-color: $mask-color;
    }
  }
  td:first-child {
    width: 32%;
  }
  thead {
    font-size: 11px;
    text-transform: uppercase;
    color: $text-light;
    tr {
      background-color: #fff !important;
    }
    td {
      padding: 0 10px;
    }
  }
}

.information-card_table.colored {
  tbody {
    td:not(:first-child) {
      color: $main-accent;
    }
    .attention {
      td:not(:first-child) {
        color: $attent-accent;
      }
    }
  }
}


@media (min-width: 540px) {
  .product-info {
    .product-description {
      &_list {
        font-size: 15px;
      }
    }
  }
  .information-card_table {
    width: 100%;
  }
}

@media (min-width: 768px) {
  .information-card {
    &_title {
      padding-left: 40px;
    }
  }
  .product-info {
    display: flex;
    .image-wrap {
      width: 45%;
      margin-bottom: 0;
    }
    .product-description {
      width: 55%;
      text-align: left;
    }
  }
}

@media (min-width: 992px) {
  .product-info {
    .product-description {
      padding: 30px 40px;
      .title {
        font-size: 24px;
      }
    }
  }
  .information-card {
    &.product-strength {
      width: 42%;
    }
    &.product-price-changes {
      width: 55%;
    }
  }
}

@media (min-width: 1200px) {
  .product-info {
    //width: 80%;
  }
  .information-card {
    &.product-strength {
      width: 50%;

    }
    &.product-price-changes {
      width: 48%;
      margin-right: 2%;
    }
  }
}
@media (min-width: 1500px) {
  .product-info {
    width: 65%;
  }
  .information-card {
    &.product-price-changes {
      width: 33%;
      margin-right: 0;
    }
  }

}

@media (min-width: 1860px) {
  .product-info {
    width: 49.5%;
  }
  .information-card {
    &.product-strength {
      width: 20.5%;
    }
    &.product-price-changes {
      width: 27%;
      margin-left: 0;
      margin-right: 0;
    }
  }
}
