/*  SEARCH BAR */

.search-bar {
  display: none;
  background-color: #fff;
  width: 100%;
  .search-form {
    padding-top: 20px;
    .custom-input-wrap {
      margin: 0 15px 0;
      font-size: 15px;
    }
  }
  .keyword-label {
    font-size: 15px;
    line-height: 40px;
    margin-bottom: 0;
    padding: 0 15px;
    white-space: nowrap;
    font-family: "ProximaNovaSemibold";
  }
  .icon_search {
    position: relative;
    top: 2px;
    display: inline-block;
    width: 16px;
    height: 16px;
    margin-right: 10px;
    background: url("../img/icon_search.svg");
    background-size: cover;
  }
}

.select-order-wrap {
  display: none;
  border-top: 1px solid #EDF1F5;
  .display-order-label {
    display: inline-block;
    font-size: 15px;
    padding: 10px 15px 0 15px;
    margin-bottom: 0;
    white-space: nowrap;
  }
}

.select-keyword-wrap {
  height: 72px;
  margin-top: 20px;
  border-top: 1px solid #f5f6fa;
  border-bottom: 1px solid #f5f6fa;
}
.custom-input-wrap {
  .icons {
    color: $main-accent;
    font-size: 20px;
  }
}
/* --------------
----------------*/
$select-color: $text-medium;
$select-background: #fff;
$select-width: 217px;
$select-height: 72px;

.select-hidden {
  display: none;
  visibility: hidden;
  padding-right: 10px;
}

.select {
  cursor: pointer;
  display: inline-block;
  position: relative;
  font-size: 15px;
  color: $select-color;
  width: 220px;
  height: $select-height;
}

.select-styled {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  background-color: $select-background;
  font-family: "ProximaNovaSemibold";
  padding: 8px 35px 8px 24px;
  transition: all 0.2s ease-in;
  user-select: none;
  &:after {
    content: "";
    width: 8px;
    height: 8px;
    border-bottom: 2px solid $main-accent;
    border-right: 2px solid $main-accent;
    transform: rotate(45deg);
    transition: transform .2s ease;
    position: absolute;
    top: 32px;
    right: 26px;
  }
  &:hover {
    background-color: darken($select-background, 2);
  }
  &:active, &.active {
    background-color: #fff;
    &:after {
      transform: rotate(225deg);
    }
  }
}


.select-options {
  display: none;
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  z-index: 999;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #fff;
  box-shadow: 0 4px 8px 1px rgba(0, 0, 0, 0.05);
  li {
    margin: 0;
    padding: 12px 0;
    text-indent: 15px;
    transition: all 0.2s ease-in;
    &:hover {
      color: #fff;
      background: $main-accent;
    }
    &[rel="hide"] {
      display: none;
    }
  }
}
.form-group {
  margin-bottom: 0;
}
.search-bar .search-stores {
  padding-bottom: 30px;
  .custom-input {
    width: 250px;
  }
}

@media (min-width: 460px) {
  .search-bar {
    .search-stores {
      .custom-input {
        width: 340px;
      }
    }
  }
}

@media (min-width: 992px) {
  .search-bar {
    display: flex!important;
    .search-form {
      padding-left: 30px;
      padding-top: 0;
      padding-bottom: 0;
      border-left: 2px solid $body-bg;
      border-right:1px solid $body-bg;
      .custom-input-wrap {
        margin: 0 25px 0;
      }
    }
    .form-group {
      display: flex;
      align-items: center;
      height: 72px;
    }
    .keyword-label {
      padding: 0;
    }
  }
  .select-keyword-wrap {
    width: auto;
    height: 72px;
    border-left: 1px solid $body-bg;
    border-top: none;
    border-bottom: none;
    margin-top: 0;
  }

  .select {
    width: $select-width;
  }
  .select-styled {
    &:after {
      top: 31px;
    }
  }
}


@media (min-width: 1200px) {
  .search-bar {
    display: flex;
    justify-content: space-between;
    height: 72px;
    .search-form {
      .custom-input-wrap {
        //width: 220px;
      }
    }
  }

  .select-order-wrap {
    display: block;
    border-left: 1px solid $body-bg;
    border-top: none;
    .display-order-label {
      display: none;
    }
  }
}