/*  SIDEBAR */

.sidebar {
  display: none;
  width: 100%;
  background-color: #fff;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    width: $sidebar-width;
    background-color: #fff;
    height: 100%;
    z-index: -1;
  }
}

.sidebar-group {
  max-width: $sidebar-width;
  padding: 30px 22px;
  &>header {
    display: flex;
    justify-content: space-between;
  }
  &_title {
    margin: 0;
    color: $text-light;
    font-size: 13px;
    line-height: 16px;
    letter-spacing: 1px;
    font-family: "ProximaNovaSemibold";
    text-transform: uppercase;
    &__tooltip {
      display: inline-block;
      width: 20px;
      height: 20px;
      text-align: center;
      line-height: 20px;
      border-radius: 50%;
      border: 1px solid $disabled;
      cursor: pointer;
      transition: all .2s ease;
      &:hover {
        border: 1px solid $main-accent;
        color: #fff;
        background-color: $main-accent;
      }
    }

  }
  &_toggler {
    position: relative;
    display: block;
    width: 20px;
    height: 20px;
    border: 2px solid #27C686;
    border-radius: 2px;
    cursor: pointer;
    transition: background-color .2s ease;
    &:hover {
      background-color: rgba(0, 0, 0, 0.05);
    }
    &:before {
      content: '';
      position: absolute;
      bottom: 2px;
      left: 7px;
      height: 0;
      width: 0;
      color: transparent;
      border-bottom: 2px solid;
      border-left: 2px solid;
      transform-origin: 0 100%;
      transform: rotateZ(-45deg);
      transition: color .2s ease, width .2s cubic-bezier(.36,.11,.68,1.64), height .2s cubic-bezier(.36,.11,.68,1.64);
    }
    &.active {
      &:before {
        color: $main-accent;
        height: 9px;
        width: 22px;
      }
    }
  }
  &_item {
    .current-date {
      position: relative;
      margin-top: 10px;
      font-size: 15px;
      line-height: 18px;
      color: $text-light;
      cursor: pointer;
      &_icon{
        width: 18px;
        display: inline-block;
        color: $main-accent;
        font-size: 18px;
      }
      &_value {
        margin-left: -5px;
        vertical-align: middle;
      }
      &:before {
        content: '';
        position: absolute;
        right: 5px;
        top: 5px;
        width: 8px;
        height: 8px;
        border-bottom: 2px solid $main-accent;
        border-right: 2px solid $main-accent;
        transform: rotate(45deg);
        transition: transform .2s ease;
        pointer-events: none;
      }
      &:hover {
        color: $text-medium;
        &:before {
          transform: rotate(405deg);
        }
      }
    }
    .item_header {
      display: flex;
      justify-content: space-between;
      margin-top: 25px;
      transition: color .2s ease;
      .title {
        margin-bottom: 0;
        font-size: 15px;
        font-family: "ProximaNovaSemibold";
        color: $text-medium;
        transition: color .2s ease;
      }
      .value {
        font-size: 15px;
        color: $text-light;
      }
    }

  }
  &+.sidebar-group {
    border-top: 2px solid $body-bg;
  }
}


.sidebar-group.disabled {
  .sidebar-group_title {
    color: $main-accent;
  }
  .item_header {
    color: $disabled;
    .title, .value {
      color: $disabled;
    }
  }
  .sidebar-group_item .current-date {
    pointer-events: none;
    &:before {
      border-bottom: 2px solid $disabled;
      border-right: 2px solid $disabled;
    }
  }
  .sidebar-group_item.date-select {
    pointer-events: none;
    .select-styled {
      color: $disabled;
      &:after {
        border-bottom: 2px solid $disabled;
        border-right: 2px solid $disabled;
      }
    }
  }
  .current-date_icon, .current-date {
    color: $disabled;
  }
  .asRange {
    pointer-events: none;
    background-color: $disabled;
    .asRange-pointer {
      background-color: $disabled;
      &:before, &:after {
        background: $disabled;
      }
    }
    .asRange-selected {
      background-color: $disabled;
    }
  }
}


.asRange {
  position: relative;
  width: calc(100% - 8px);
  height: 6px;
  margin: 10px auto 0;
  background-color: $body-bg;
  transition: background-color .2s ease;

  .asRange-pointer {
    top: 0;
    background-color: #fff;
    cursor: pointer;
    transition: background-color .2s ease;
    &:before {
      top: -4px;
      right: -4px;
      bottom: -4px;
      left: -4px;
      background: $main-accent;
      transition: background-color .2s ease;
    }
    &:after {
      top: 1px;
      right: 1px;
      bottom: 1px;
      left: 1px;
    }
    &:focus {
      outline: none;
    }
  }
  .asRange-selected {
    height: 6px;
    top: 0;
    background-color: $main-accent;
  }
}

.daterangepicker {
  border-radius: 0;
  border: none;
  box-shadow: 0 5px 8px 1px rgba(237,239,241,0.5);
  .drp-selected {
    padding: 15px;
    display: block;
    text-align: center;
    font-size: 15px;
  }
  .calendar-table table {
    border-spacing: 0 4px;
    border-collapse: separate;
  }
  .drp-calendar {
    box-shadow: 0 5px 8px 1px rgba(237,239,241,0.5);
  }
  .ranges {
    li {
      padding: 0 16px;
      font-size: 14px;
      height: 32px;
      line-height: 32px;
      &.active {
        background-color: $main-accent;
      }
    }
  }
  &:before, &:after {
    display: none;
  }
  tr {
    height: 24px;
    line-height: 24px;
  }
  td.active, td.active:hover, td.in-range {
    background-color: $main-accent;
  }
  td.available:hover, th.available:hover {
    background-color: rgb(46, 237, 160);
  }
}
.sidebar-group {
  .select {
    font-size: 15px;
    height: 30px;
  }
  .select-styled {
    line-height: 30px;
    padding: 0;
    &:after {
      top: 10px;
      right: 5px;
    }
    &:active, &.active {
      background-color: #fff;
      &:after {
        transform: rotate(225deg);
      }
    }
  }
  .date-select {
    .form-group {
      position: relative;
      &:hover {
        .select-styled {
          color: #4f575f;
        }
      }
    }
    .select {
      height: 40px;
    }
    .current-date_icon {
      position: absolute;
      left: -6px;
      top: 6px;
      pointer-events: none;
      width: 24px;
      z-index: 10;
    }
    .select-styled {
      padding-left: 20px;
      color: #8d9aa8;
      font-family: "ProximaNovaRegular";
      &:after {
        top: 14px;
        right: 0;
      }
      &:hover {
        background-color: transparent;
      }
    }
  }
  .custom-input-wrap {
    width: 100%;
  }
  .custom-input {
    width: 100%;
    font-size: 15px;
  }
}


@media (min-width: 992px) {
  .sidebar {
    display: block!important;
    width: $sidebar-width;
    float: left;
  }
  .daterangepicker {
    .drp-selected {
      display: inline-block;
    }
  }
}