
.store-info {
  margin-bottom: 20px;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 5px 8px 1px rgba(237, 239, 241, 0.5);
  overflow: hidden;
  .image-wrap {
    img {
      width: 100%;
    }
  }
}

.store-description {
  padding: 10px 15px 30px;
  text-align: center;
  .title {
    font-size: 24px;
    margin-top: 15px;
    color: $text-medium;
    font-family: "ProximaNovaSemibold";
  }
  &_text {
    padding-top: 20px;
    text-align: justify;
    font-size: 14px;
  }
}

.info-container {
  width: 100%;
  &_list {
    list-style-type: none;
    padding: 15px 10px 10px 25px;
    font-size: 15px;
    li {
      margin-bottom: 10px;
    }
    .key {
      font-family: "ProximaNovaSemibold";
    }
    .val {

    }
  }
}

.store-chart_nav {
  max-width: 100%;
  justify-content: flex-end;
  li {
    margin-left: 20px;
  }
}

.store-chart {
  width: 100%;
  min-height: 300px;
  &_list {
    display: flex;
    li {
      width: 40px;
      margin-left: 20px;
      display: flex;
      flex-direction: column;
      text-align: center;
    }
    .visual {
      display: block;
      height: 100px;
      border-radius: 4px;
      background: linear-gradient(#4AF06F, #4BDEA3);
    }
    .value {
      margin-bottom: 24px;
      color: $text-medium;
      font-size: 21px;
      line-height: 25px;
    }
    .date {
      margin-top: 16px;
      color: $text-light;
      font-size: 13px;
      line-height: 16px;
    }
  }
}

.information-card.apps-container {
  width: 100%;
}

.apps-wrapper {
  position: relative;
}

.used-apps {
  &_nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    list-style-type: none;
    padding: 15px 10px 5px 20px;
    margin-bottom: 0;
  }
  &_list {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-height: 290px;
    list-style-type: none;
    padding: 0;
    li {

    }
  }
  &_link {
    display: flex;
    max-width: 274px;
    padding: 10px;
    margin: 0 0 15px;
    border: 2px solid transparent;
    border-radius: 44px;
    font-family: ProximaNovaSemibold;
    transition: all .4s ease;
    &:hover {
      border: 2px solid #EDF1F5;
      text-decoration: none;
      box-shadow: 0 5px 8px 1px rgba(237, 239, 241, 0.5);
    }
    .app-name {
       margin-top: 5px;
       margin-bottom: 0;
       max-height: 38px;
       overflow: hidden;
       color: $text-medium;
       font-size: 15px;
       line-height: 19px;
     }
    .app-tag {
      margin-left: 15px;
      color: $text-light;
      font-size: 12px;
      line-height: 12px;
      text-transform: uppercase;
    }
  }
  &_icon {
    display: inline-block;
    width: 48px;
    height: 48px;
    margin-right: 10px;
    border-radius: 50%;
  }
  &_side-nav {
    position: absolute;
    top: 0;
    bottom: 20px;
    right: 0;
    width: 80px;
    text-align: center;
    .side-nav_trigger {
      width: 40px;
      height: 40px;
      margin: 0 auto;
      text-align: center;
      font-size: 35px;
      line-height: 42px;
      font-family: ProximaNovaBold;
      color: #fff;
      background-color: $main-accent;
      border-radius: 50%;
      cursor: pointer;
      user-select: none;
      transform: rotateZ(0);
      transition: transform .2s ease;
      &:hover {
        opacity: .6;
      }
    }
    .side-nav_list {
      text-align: center;
      padding: 0;
      list-style-type: none;
      li {
        width: 50px;
        height: 50px;
        margin: 0 auto;
        margin-top: 10px;
        background-color: #aeffa9;
        border-radius: 50%;
        cursor: pointer;
        transform-origin: 100% 50%;
        transform: scale(0) translateX(100px);
        transition: all .2s ease;
        background-size: cover;
        opacity: .5;
        &.home {
          background-image: url("../img/app-used-nav-1.jpg");
        }
        &.time {
          background-image: url("../img/app-used-nav-2.jpg");
        }
        &.settings {
          background-image: url("../img/app-used-nav-3.jpg");
        }
        &:hover {
          opacity: 1;
        }
      }
    }
    &.visible {
      .side-nav_trigger {
        transform: rotateZ(135deg);
      }
      .side-nav_list li {
        transform: scale(1) translateX(0);
      }
    }
  }
}
.CustomScrollbar {
  padding: 0 12px;
}
.mCSB_scrollTools.mCSB_scrollTools_horizontal {
  height: 18px;
  right: 5px;
  left: 5px;
}

#mCSB_1_dragger_horizontal {
  position: absolute;
  min-width: 16px !important;
  display: block;
  width: 16px !important;
  height: 16px !important;
  max-width: 680px;
  left: 23px;
}

.mCSB_scrollTools.mCSB_scrollTools_horizontal
.mCSB_dragger .mCSB_dragger_bar {
  width: 16px;
  height: 16px;
  margin: 2px auto;
  border: 5px solid $text-light;
  background-color: #fff;
}

.mCSB_scrollTools.mCSB_scrollTools_horizontal .mCSB_draggerRail {
  width: 100%;
  height: 6px;
  margin: 7px 0;
  background-color: #EBECF0;
}

@media (min-width: 425px) {
  .used-apps {
    &_list {
      //padding: 0;
    }
    &_link {
      max-width: 300px;
      margin: 0 10px 2px 0;
      .app-name {
        margin-top: 12px;
        font-size: 16px;
      }
    }
    &_icon {
      width: 56px;
      height: 56px;
      margin-right: 15px;
    }
  }
  .CustomScrollbar {
    padding: 0 20px 0 13px;
  }
  .mCSB_scrollTools.mCSB_scrollTools_horizontal {
    right: 20px;
    left: 20px;
  }
  .information-card.table-container.store-categories {
    td {
      text-align: left;
      padding-left: 25px;
    }
  }

}

@media (min-width: 768px) {
  .store-description {
    &_text {
      font-size: 15px;
    }
    .store-card_link {
      width: 320px;
    }
  }
  .info-container {
    width: 48%;
    &_list {
      padding-left: 40px;
    }
  }
  .information-card.table-container.store-categories {
    width: 48%;
    td {
      text-align: left;
      padding-left: 40px;
    }
  }
  .used-apps {
    &_link {
      max-width: 335px;
      margin: 0 50px 2px 0;
    }
    &_nav {
      padding: 15px 10px 5px 32px;
      .information-card_nav__button {
        padding: 0 10px;
      }
    }
  }
  .CustomScrollbar {
    padding: 0 25px 0;
  }
}

@media (min-width: 992px) {
  .store-info {
    display: flex;
    .image-wrap {
      width: 40%;
      flex-shrink: 0;
    }
  }
  .store-description {
    text-align: left;
    padding: 30px 50px 30px 40px;
  }

}

@media (min-width: 1200px) {
  .store-info {
    width: 73%;
  }
  .info-container {
    width: 26%;
  }
  .information-card.table-container.store-categories {
    width: 48%;
  }
  .information-card {
    &.store-chart {
      width: 50%;
    }
  }
  .information-card {
    &.apps-container {
      width: 100%;
    }
  }
  .used-apps {
    &_nav {
      //width: calc(100% - 180px);
    }
    &_side-nav {
      padding-left: 20px;
      width: 180px;
      background: linear-gradient(to right, transparent 0%, #fff 20%);
      .side-nav_trigger {
        display: none;
      }
      .side-nav_list {
        text-align: center;
        padding: 0;
        li {
          width: 62px;
          height: 62px;
          margin-top: 15px;
          transform: scale(1) translateX(0);
        }
      }
    }
  }
  .CustomScrollbar {
    //width: calc(100% - 150px);
  }
}

@media (min-width: 1860px) {
  .store-info {
    width: 49%;
  }
  .info-container {
    width: 20%;
  }
  .information-card.table-container.store-categories {
    width: 27%;
  }
  .information-card {
    &.store-chart {
      width: 49%;
    }
  }
  .information-card {
    &.apps-container {
      width: 49%;
    }
  }
}